import React from "react"
import styles from "./threeColumns.module.sass"

class ThreeColumnSection extends React.Component {
  render() {
    const { children, title, subtitle, backgroundColor, noPadding } = this.props
    return (
      <div
        className={styles.threeColumns}
        style={{
          background: backgroundColor ? backgroundColor : "",
          padding: noPadding ? "0 0 0 0" : "",
        }}
      >
        {title && (
          <div className={styles.titleContainer}>
            <h2>{title}</h2>
            {subtitle && <h5>{subtitle}</h5>}
          </div>
        )}
        <div className={styles.inner}>{children}</div>
      </div>
    )
  }
}

export default ThreeColumnSection
